@import "../../../../App/mixin";

.formReset {
	z-index: 10;
	display: grid;
	row-gap: 32px;

	max-width: 574px;
	width: 100%;
	padding: 40px;
	margin: 0 auto;

	background: var(#{--body-background-default});
	border-radius: 10px;

	box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px,
		rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px,
		rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.receivePassToEmail {
	@include DescriptionText;
	color: var(#{--text-color-default});
}

.titleReset {
	@include TitleText;
	color: var(#{--text-color-default});
}

.buttonReset {
	padding: 16px;
	border-radius: 10px;
	color: #FFFFFF;
	background-color: #7B61FF;
}

@media (max-width:320px) {
	.titleReset {
		font-size: 20px;
	}
}